import {Swiper, EffectCoverflow } from "swiper";
Swiper.use([EffectCoverflow]);

const swiper = new Swiper('.swiper-container', {
    effect: 'coverflow',
    centeredSlides: true,
    slidesPerView: 1.6,
    spaceBetween: 0,
    loop: true,
    speed: 600,
    autoHeight: false,


    coverflowEffect: {
        rotate: 0,
        stretch: 0,
        depth: 25,
        modifier: 25,
        slideShadows: false,
    },
});

